<!--Settings下属存储计划组件-->
<template>
    <div>
<!--        <h2>存储计划</h2>-->
        <el-collapse v-model="activeNames" @change="handleChange">
            <el-collapse-item title="SIS与GDS(气体报警)测点存储计划" name="1">
                <el-form>
                    <el-form-item label="下次备份执行时间">
                        <el-row>
                            <el-col :span="6">
                                <el-date-picker
                                    v-model="SISGDSStartTime"
                                    type="datetime"
                                    placeholder="选择起始日期时间"
                                    align="right"
                                    value-format="yyyy-MM-dd HH:mm:ss"
                                    :picker-options="pickerOptions"
                                    size="mini">
                                </el-date-picker>
                            </el-col>
                            <el-col :span="6">
                                SIS与GDS测点备份间隔天数
                                <el-input-number v-model="num" @change="handleChange"
                                                 :min="15" :max="60" label="SIS与GDS测点备份间隔天数" size="mini">
                                </el-input-number>
                            </el-col>
                        </el-row>
                    </el-form-item>
                            <el-button size="mini" type="danger">立即备份并清空online数据库</el-button>
                </el-form>
            </el-collapse-item>
            <el-collapse-item title="DCS测点存储计划" name="2">
                <el-form>
                    <el-form-item label="下次备份执行时间">
                        <el-row>
                            <el-col :span="6">
                                <el-date-picker
                                    v-model="DCSStartTime"
                                    type="datetime"
                                    placeholder="选择起始日期时间"
                                    align="right"
                                    value-format="yyyy-MM-dd HH:mm:ss"
                                    :picker-options="pickerOptions"
                                    size="mini">
                                </el-date-picker>
                            </el-col>
                            <el-col :span="6">
                                DCS测点备份间隔天数
                                <el-input-number v-model="DCSnum" @change="handleChange"
                                                 :min="15" :max="60" label="DCS测点备份间隔天数" size="mini">
                                </el-input-number>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <el-button size="mini" type="danger">立即备份并清空online数据库</el-button>
                </el-form>
            </el-collapse-item>
            <el-collapse-item title="事件存储计划" name="3">
                <el-form>
                    <el-form-item label="下次备份执行时间">
                        <el-row>
                            <el-col :span="6">
                                <el-date-picker
                                    v-model="EventStartTime"
                                    type="datetime"
                                    placeholder="选择起始日期时间"
                                    align="right"
                                    value-format="yyyy-MM-dd HH:mm:ss"
                                    :picker-options="pickerOptions"
                                    size="mini">
                                </el-date-picker>
                            </el-col>
                            <el-col :span="6">
                                事件备份间隔天数
                                <el-input-number v-model="EventNum" @change="handleChange"
                                                 :min="15" :max="60" label="DCS测点备份间隔天数" size="mini">
                                </el-input-number>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <el-button size="mini" type="danger">立即备份并清空online数据库</el-button>
                </el-form>
            </el-collapse-item>
            <el-collapse-item title="视频存储计划" name="4">
                <div><strong>再琢磨琢磨吧</strong></div>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script>
    export default {
        name: "StorageSetting",
        data() {
            return {

                // default collapse open on SIS and GDS point storage plan
                activeNames: ['1'],
                // for default or custom storage setting
                value1: true,
                // start time for sis and gds points
                SISGDSStartTime: '',
                // SIS GDS time period
                num: 15,

                // DCS start time
                DCSStartTime: '',
                // SIS GDS time period
                DCSnum: 15,

                // event start time
                EventStartTime: '',
                // Event time period
                EventNum: 15,
                //picker option for sis and gds start time
                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }]
                },
            }
        },
        components: {

        },
        methods: {
            handleChange(val) {
                // eslint-disable-next-line no-console
                console.log(val);
            },
        },
        created() {
        },
        mounted() {
        },
    }
</script>

